import axios from 'axios';

import { GET_POST, ADD_POST } from './types';

// GET POSTS
export const getPost = () => dispatch => {
    axios
        .get('/api/post/')
        .then(res => {
            dispatch({
                type: GET_POST,
                payload: res.data
            });
        })
        .catch(err => console.log(err));
};

// ADD POST
export const addPost = (post) => dispatch => {
    axios
        .post('/api/post/', post)
        .then(res => {
            dispatch({
                type: ADD_POST,
                payload: res.data
            });
        })
        .catch(err => console.log(err));
};
